
.p-voice-contents {

  @include mq(pc){
    position: relative;
  }

  
  
  &__bg {
    position: relative;
    margin-bottom: 30px;

    @include mq(pc){
      margin-bottom: 0;
      padding-bottom: 110px;
    }
  }
  
  &__first {

    
    @include mq(tb){
      margin-top: 50px;
      background: url("../images/common/bg-shape03.svg")no-repeat right bottom -15%;
      background-size: 50%;
    }
    @include mq(pc){
      padding-bottom: 110px;
      background-size: auto;
    }

  }

  &__left {
    padding-top: 60%;
    background: url("../images/common/voice-1.jpg")no-repeat left;
    background-size: cover;

    
    @include mq(pc){
      padding-top: 37.5%;
      background-size: contain;
    }
  }
  
  &__right {
    padding-top: 60%;
    background: url("../images/common/voice-2.jpg")no-repeat right;
    background-size: cover;

    @include mq(pc){
      padding-top: 37.5%;
      background-size: contain;
    }
  }

  &__item {
    
    

    @include mq(tb){
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    
    
    @include mq(pc){
      margin-top: -55px;
    }
  }

}