.c-information {
  width: 100%;
  padding: 16px ;
  border: 1px solid #555555;
  
  @include mq(pc){
    display: flex;
    justify-content: space-between;
    padding: 75px 77px 66px 94px;
  }

  &__heading {
    font-family: $japanFontSefif;
    font-size: $pc-h3;
    letter-spacing: .1em;
    line-height: 1;
    padding-right: 24px;
    margin-bottom: 24px;
    white-space: nowrap;
    text-align: center;
    user-select: none;

    @include mq(pc){
      text-align: left;
      padding-right: 90px;
    }
  }

  &__item {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
    
    a {
      display: flex;
    }
  }

  &__date {
    position: relative;
    padding-right: 27px;
    
    
    &::after {
      content: "";
      width: 3px;
      height: 16px;
      background-color: #555555;
      position: absolute;
      top: 7px;
      right: 0;
    }
  }

  &__title {
    padding-left: 20px;
    letter-spacing: .1em;
    line-height: 1.6;
    font-weight: 400;

    @include mq(pc){
      line-height: 2;
    }
  }

}