.l-service {
  padding-top: 100px;
  padding-bottom: 120px;
  background:  $accentColor;

  @include mq(pc){
    padding-top: 155px;
  }


  &__container {
    @include mq(tb){
      padding: 0 15px;
    }

    @include mq(xl){
      padding: 0px;
    }
  }
  

  &__bg {

    @include mq(tb){
      background:  url("../images/common/bg-shape05.svg")no-repeat;
      background-position: 0 calc(50%+15px) ;
    }
  }

  &__heading {
    margin-bottom: 75px;
  }


  &__menus {
    display: flex;
    flex-direction: column;
    padding: 0 30px;
    

    @include mq(tb){
      flex-direction: row;
      justify-content: space-between;
      padding: 0;
    }


  }
  
}