.p-footer-nav {
  display: flex;
  font-size: 1.4rem;
  flex-direction: column;
  justify-content: space-between;
  
  @include mq(tb){
    flex-direction: row;
    justify-content: space-between;
  }

  @include mq(pc){
    flex-direction: row;
    justify-content: flex-start;
  }


  &__left {
    margin-right: 56px;


  }

  &__center {
    margin-right: 56px;
  }

  &__menus {
    margin-bottom: 28px;
  }

  &__heading {
    margin-bottom: 18px;
    letter-spacing: .1em;
    line-height: 1;
  }

  &__item {
    margin-bottom: 8px;
    letter-spacing: .1em;
    line-height: 1;
  }



}