
.hamburger {

  padding: 0;

  .hamburger-inner {
    background-color: $baseTextColor;
  
    &::before,
    &::after {
      background-color: $baseTextColor;
    }
  
  
  }
  
  &.is-active {
  
    .hamburger-inner {
      background-color: $baseTextColor;
  
      &::before,
      &::after {
        background-color: $baseTextColor;
      }
    }
  }
}

