.p-contact-heading {
  margin-bottom: 60px;
  text-align: center;
  
  &__main {
    font-family: $japanFontSefif;
    font-size: $pc-h1;
    font-weight: 300;
    letter-spacing: .1em;
    line-height: 1;
    margin-bottom: 37px;
    user-select: none;
  }

  &__sub {
    letter-spacing: .01em;
    line-height: 1;
    user-select: none;

    @include mq(tb){
      letter-spacing: .1em;
    }
  }
}