.c-service-item {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #555555;
  padding: 35px 15px 45px;
  margin-bottom: 45px;
  
  &:last-of-type{
    margin-bottom: 15px;
  }
  
  @include mq(tb){
    width: 31%;
    padding: 65px 30px 75px;
    margin-bottom: 0;

    &:last-of-type{
      margin-bottom: 0px;
    }
    
  }

  @include mq(pc){
    padding: 65px 55px 75px;
  }

  &__icon {
    display: block;
    height: 65px;
    margin-bottom: 40px;
  }


  &__title {
    letter-spacing: .1em;
    line-height: 1;
    margin-bottom: 24px;
    user-select: none;
  }

  &__text {
    letter-spacing: .1em;
    line-height: 1.6;
    user-select: none;
  }
}