
.p-about-img {

  @include mq(pc){
    display: flex;
  }

  &__left {
    width: 100%;
    padding: 15px 30px;

    @include mq(pc){
      width: 62.5%;
      padding: 0;
    }

    img {
      width: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;'
    }
  }

  &__right {
    width: 100%;
    padding: 15px 30px;

    @include mq(pc){
      width: 37.5%;
      padding: 0;
    }

    img {
      width: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;'
    }
  }
}