
.p-about-content {
  padding: 40px 30px;


  @include mq(pc){
    padding-top: 50px;
    padding-left: 28%;
    padding-right: 12%;
    width: 70%;
  }

  &__lead {
    font-size: 1.9rem;
    letter-spacing: .05em;
    line-height: 1.5;
    margin-bottom: 24px;
    user-select: none;

    @include mq(tb){
      font-size: $pc-h3;
      letter-spacing: .1em;
    }

    @include mq(pc){
      font-size: $pc-h1;
    }
  }

  &__text {
    letter-spacing: .1em;
    margin-bottom: 40px;
    user-select: none;
  }

  &__img {
    width: 100%;
    padding: 15px 30px;

    @include mq(pc){
      padding: 0;
      padding-top: 55px;
      width: 30%;
    }

    img {
      width: 100%;

      @include mq(pc){
        height: 600px;
        object-fit: cover;
        object-position: 100% 0;
        font-family: 'object-fit: cover; object-position: 100% 0;'
      }
    }
}


}

