.c-voice-item {
  background-color: rgba(0,0,0,.8);
  padding: 30px 15px;

  @include mq(tb){
    padding: 30px;
  }
  
  @include mq(xl){
    padding: 71px 100px 72px 87px;
    width: 42.5%;
  }

  &__title {
    font-size: $pc-h4;
    line-height: 1;
    letter-spacing: .1em;
    margin-bottom: 25px;
    user-select: none;

    @include mq(tb){
      font-size: $pc-h3;
    }
  }

  &__category {
    line-height: 1;
    letter-spacing: .1em;
    margin-bottom: 14px;
    user-select: none;
  }

  &__text {
    margin-bottom: 20px;
    letter-spacing: .1em;
    line-height: 1.3;
    user-select: none;

    @include mq(tb){
      line-height: 1.6;
    }
  }

}