.l-about {
  position: relative;
  padding-top: 100px;

  @include mq(pc){
    padding-top: 188px;
  }

 
  &__container {
    display: flex;

    @include mq(pc){
      padding: 0 15px;
    }
  }

  &__heading {
    width: 100%;
    display: flex;
    justify-content: center;
  

   @include mq(pc){
     display: block;
     position: absolute;
     top: 0;
     left: 0; 
     margin-left: 30px;
    }

    @include mq(xl){
      margin-left: 0;
    }
  }

  &__upper {
    
    @include mq(pc){
      background: url("../images/common/bg-shape02.svg")no-repeat left bottom -5%;
      display: flex;
    }
  }




}