.l-header {
  // position: fixed;
  width: 100%;
  height: 100px;
  z-index: map-get($layer, "header" );
  font-family: $japanFontSanSefif;
  background-color: $mainColor;


  @include mq(pc){
    height: 200px;
  }


  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    height: 100%;
    // z-index: 1;

    @include mq(pc){
      padding: 0 30px;
    }

    @include mq(xl){
      padding: 0 100px;
    }
  }
 
  &__nav {
    display: flex;
    flex-direction: row;
    align-items: center;

    @include mq(pc){
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
    }
  }

  &__head {
    display: none;

    @include mq(pc){
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;
      margin-top: 8px;
    }

    @include mq(pc){
      flex-direction: row;
      justify-content: flex-end;
    }
  }

  &__tel {
    margin-right: 28px;
    margin-bottom: 16px;

    @include mq(pc){
      margin-bottom: 0;
    }
  }

  &__buttons {
    display: flex;
    flex-direction: row;
  }

  &__button--mr {
    margin-right: 12px;
  }

  &__foot {
    display: none;

    @include mq(pc){
      display: block;
    }
    
  }

  &__menu {
    display: flex;
    justify-content: flex-end;
  }

  &__menu-item {
    margin-right: calc(100vw * 0.04);

    &--last {
      margin-right: 0;
    }

  }

  &__hamburger {
    margin-left: 32px;
    
    @include mq(pc){
      display: none;
    }
  }
  

}

