.p-contact-item  {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
  border-bottom: 1px solid $baseTextColor;

  &:last-of-type{
    margin-bottom: 45px;

    @include mq(tb){
      margin-bottom: 0;
    }
  }

  @include mq(tb){
    width: 50%;
    border-bottom: none;
  }
  
  
  &:first-child {
    @include mq(tb){
      border-bottom: none;
      border-right: 1px solid $baseTextColor;
    }
  }

  &__text {
    letter-spacing: .1em;
    line-height: 1.6;
    margin-top: 16px;
    margin-bottom: 23px;
    text-align: center;
    user-select: none;

    @include mq(pc){
      line-height: 1;
      text-align: left;
    }
  }

  &__tel {
    font-family: $englishFont;
    font-size: $pc-h3;
    font-weight: 500;
    letter-spacing: .1em;
    line-height: 1;
    margin-bottom: 24px;
    user-select: none;

    @include mq(tb){
      font-size: $pc-h2;
    }
  }

  &__description {
    letter-spacing: .1em;
    line-height: 1.6;
    text-align: center;
    user-select: none;
    
    @include mq(pc){
      line-height: 1;
    }
  }

  &__button {
    letter-spacing: .1em;
    line-height: 1;
    padding: 20px 30px;
    border: 1px solid $baseTextColor;

    @include mq(tb){
      padding: 30px 80px;
    }
  }

}