.c-collection-slider-item {
  
  &__groupe {
    width: 80%;
    height: auto;
    margin: 0 auto;

    @include mq(pc){
      width: 85%;
    }
  }

  &__img {
    margin-bottom: 50px;
  }

  &__name {
    font-size: 1.8rem;
    line-height: 1;
    letter-spacing: 0.1em;
    margin-bottom: 21px;

    @include mq(pc){
      font-size: $pc-h4;
    }
    
  }

  &__category {
    line-height: 1;
    letter-spacing: 0.1em;
    display: inline-block;
    background-color: #272d30;
    padding: 9px 16px;
  }

}