

.c-header-button {
  display: flex;
  width: 140px;
  height: 50px;
  border: 1px solid #555555;
  color: $baseTextColor;
  background-color: $mainColor;
  font-size: $sp-base;
  justify-content: center;
  align-items: center;
  transition: all .3s;

  @include mq(tb){
    width: 160px;
    height: 50px;
  }

  &:hover {
    color: $mainColor;
    background-color: $baseTextColor;
  }
  
}