.p-footer-left {

  margin-bottom: 45px;

  @include mq(pc){
    margin-bottom: 0;
  }

  &__logo {
    margin-bottom: 16px;
  }

  &__address,
  &__tel {
    margin-bottom: 8px;
    letter-spacing: .1em;
    line-height: 1;
    font-size: 1.4rem;
    user-select: none;
  }

  &__tel {
    letter-spacing: 0;
    margin-bottom: 38px;
  }

  &__button {
    display: inline-block;
    border: 1px solid #666666;
    letter-spacing: .1em;
    line-height: 1;
    padding: 16px 45px;


    &:hover {
      color: #666666;
      background-color: $baseTextColor;
      border: 1px solid $baseTextColor;
    }
  }

}