
.c-heading {
  font-family: $japanFontSefif;
  position: relative;
  user-select: none;

  @include mq(pc){
    writing-mode: vertical-rl;
  }
  
  &__main {
    position: relative;
    font-size: $pc-h3;
    font-weight: 300;
    letter-spacing: 0.1em;
    line-height: 1;
    padding-left: 35px;

    @include mq(tb){
      padding-left: 56px;
      font-size: $pc-h2;
    }
    

    @include mq(pc){
      padding-left: 0;
      padding-top: 56px;
      line-height: 1.6;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      background: url("../images/common/heading-mark.svg")no-repeat center;
      background-size: contain;
      width: 25px;
      height: 30px;
      display: inline-block;

      @include mq(tb){
        width: 30px;
        height: 35px;
      }

      @include mq(pc){
        left: 50%;
        transform: translateX(-50%);
      }

    }
  }

  &__sub {
    font-size: $pc-h4;
    letter-spacing: 0.1em;
    padding-left: 35px;

    @include mq(tb){
      font-size: $pc-h3;
      padding-left: 56px;
    }
    

    @include mq(pc){
      padding-left: 0;
      padding-top: 56px;
    }
  }
} 