
//メディアクエリの設定
/* ブレークポイント
_______________________________________________________________________________________________________*/ 

$screen-xl: 1400px;
$screen-pc: 1024px;
$screen-tb: 768px;
$screen-sp: 360px;

$bp-list: (

 sp: screen and (min-width: #{$screen-sp}),
 tb: screen and (min-width: #{$screen-tb}),
 pc: screen and (min-width: #{$screen-pc}),
 xl: screen and (min-width: #{$screen-xl}),
 
) !default;


@mixin mq($breakpoint) {
    
  @media #{map-get($bp-list, $breakpoint)} {
    @content;
  }
}






