.c-main-slider-item {
  position: relative;
  display: flex;

  &__img {
    width: 100%;
    height: calc(100vh-200px);
    // overflow: hidden;
    // object-fit: cover;

    @include mq(pc){
      width: 75%;
    }
    @include mq(xl){
      height: 100%;
    }
    
  }


  &__text {
    position: absolute;
    bottom: 25%;
    right: auto;
    left: 30px;
    padding: 14px 32px;
    border: 1px solid $baseTextColor;
    letter-spacing: .1em;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;


    @include mq(pc){
      bottom: 20%;
      right: 0;
      left: auto;
      width: 21.7%;
      height: 12%;
    }

    @include mq(xl){
      bottom: 25.5%;
      right: 0;
    }

  }

}