.l-footer {
  width: 100%;
  padding-top: 88px;

  
  &__container {
    display: flex;
    flex-direction: column;
    padding-bottom: 80px;
    padding-left: 30px;
    padding-right: 30px;

    @include mq(pc){
      justify-content: space-between;
      flex-direction: row;
    }

    @include mq(xl){
      padding-left: 0px;
      padding-right: 0px;
  
    }

  }


  &__copy { 
    border-top: 1px solid #666666;
    text-align: center;
    line-height: 1;
    padding: 32px 0;
    user-select: none;
  }

}