.l-global-nav {
  position: fixed;
  top: 100px;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  background: rgba(221,221,221, .95);
  padding: 24px 30px;
  opacity: 0;
  visibility: hidden;
  transform: translateX(40px);
  transition: all .3s;
  z-index: map-get($layer, "sp-menu" );

  @include mq(tb){
    top: 100px;
    padding: 45px 30px;
  }

  &.is-active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0px);
  }

  &__menus {
    margin-bottom: 32px;
  }

  &__item {
    font-size: $pc-h4;
    display: flex;
    align-items: center;
    list-style: none;
    padding: 10px 0;
    color: #555555;
    border-bottom: solid 1px #555555;

    &:last-of-type {
      border-bottom: none;
    }

    @include mq(tb){
      font-size: $pc-h3;
      padding: 15px 0;
    }
    
  }

  &__foot {
    position: absolute;
    bottom: 0;
    color: #555555;

  }
 
  &__tel {
    margin: 24px auto;
  }

  &__buttons {
    display: flex;
  }

  &__mr {
    margin-right: 12px;

    @include mq(tb){
      margin-right: 24px;
    }

  }




}