.c-content-link {
  letter-spacing: .1em;
  line-height: 1;
  position: relative;

  
  &::after {
    content: "";
    display: inline-block;
    background: url("../images/common/link-shape.svg")no-repeat center;
    background-size: contain;
    width: 7px;
    height: 14px;
    position: absolute;
    top: 0;
    right: -24px;
  }
} 