.l-collection {

  .slick-dots {
    bottom: -55px;
    
    li {
      width: 10px;
      height: 10px;
      margin-right: 5px;
  
      &:last-child{
        margin-right: 0;
      }
  
      &.slick-active {
  
        button {
          background: $baseTextColor;
        }
  
      }
  
      button {
        background: #31393c;
        width: 10px;
        height: 10px;
  
        &::before {
          content: "";
          color: transparent;
        }
      }
    }
  }

  .slick-arrow {
    width: 30px;
    height: 80px;
    z-index: 10;
    @include mq(pc){
      width: 60px;
      height: 160px;
    }

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }

  }

  .slick-next {
    top: 40%;
    right: 5%;

    @include mq(pc){
      top: 40%;
      right: 25%;
    }
    &::before {
      background: url("../images/common/icon-slick-next.svg")no-repeat center;
      background-size: contain;
    }
  }

  .slick-prev {
    top: 40%;
    left: 5%;

    @include mq(pc){
      top: 40%;
      left: 25%;
    }
    
    &::before {
      background: url("../images/common/icon-slick-prev.svg")no-repeat center;
      background-size: contain;
    }
  }

  // .u-slick-arrow__prev {
