.c-header-tel {
  display: flex;
  flex-direction: column;
  color: $baseTextColor;
  user-select: none;


  &__bk  {
    color: #555555;
  }

  .c-header-tel__number {
    font-size: $pc-h2;
    font-family: $englishFont;
    font-weight: 700;
    line-height: 1;
  }

  .c-header-tel__text {
    font-size: 1.3rem;
    line-height: 1.5;
  }

  

}