
.l-main-visual {

  .slick-dots {
    position: absolute;
    bottom: 15%;

    @include mq(tb){
      position: absolute;
      bottom: 14%;
      right: 0;
      width: 14%;
      display: flex;
      justify-content: center;
    }

    @include mq(pc){
      bottom: 20%;
    }
    
  
    li {
      width: 10px;
      height: 10px;
      margin-right: 5px;
  
      &:last-child{
        margin-right: 0;
      }
  
      &.slick-active {
  
        button {
          background: $baseTextColor;
        }
  
      }
  
      button {
        background: #31393c;
        width: 10px;
        height: 10px;
  
        &::before {
          content: "";
          color: transparent;
        }
      }
    }
  }
}




