.l-voice {
  background: linear-gradient(to right, $accentColor 0%, $accentColor 50%, $mainColor 50%,$mainColor 100%,);
  padding-top: 100px;
  padding-bottom: 80px;
  position: relative;

  @include mq(pc){
    padding-top: 206px;
    padding-bottom: 115px;
  }

  &__heading {
    width: 100%;
    display: flex;
    justify-content: center;


    @include mq(pc){
      display: block;
      width: auto;
      position: absolute;
      top: -5px;
      right: -5px;
      margin-right: 30px;
    }

    @include mq(xl){
      margin-right: 0;
    }
  }

  &__contents {
    padding-top: 50px;

    @include mq(pc){
      padding-top: 0;
    }
  }



  &__more-button {
    display: flex;
    justify-content: center;
    align-items: center;
  }




 
}