*, *::before, *::after {
  box-sizing: border-box;
}


body  {
	color: $baseTextColor;
	background-color: $mainColor;
	// font-family: $japanFontSanSefif sans-serif;
	position: relative;
	font-weight: 400;
	font-size: $sp-base;
	letter-spacing: 0;
	line-height: 2.0;
	font-display: swap;
  margin: 0 auto;
  padding: 0;
	
	//高精度displayへの対応 
  @media (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 2dppx) {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }


}


#wrapper {
	position: relative;
	overflow: hidden;
}

.container {
  max-width: $layout-width-inner;
	padding: 0 15px;
	margin: 0 auto;
	position: relative;
	z-index: 1;
}

//a タグの動作設定
a {
	color: inherit;
	display: inline-block;
}

a:link, a:visited, a:hover, a:active {
  color: inherit;
  text-decoration: none;
  transition: all .3s;
}

a:hover {
  opacity: 0.7;
}

//clearfixの設定
.clearfix:after {
  content: "";
  display: block;
  clear: both;
  height: 0;
  visibility: hidden;
}

//imgタグの設定
img {
  vertical-align: middle;
  max-width: 100%;
}

/* ====================================================================================
		tablet
==================================================================================== */

@include mq(tb){

	body {
		font-size: $pc-base;
	}

	.container {
		padding: 0 20px;
	}
}

