.p-service-heading {
  text-align: center;

  &__main {
    font-family: $japanFontSefif;
    font-size: $pc-h3;
    font-weight: 300;
    letter-spacing: .1em;
    line-height: 1;
    margin-bottom: 45px;
    user-select: none;

    @include mq(pc){
      font-size: $pc-h1;
    }
  }

  &__sub {
    letter-spacing: .1em;
    padding: 0 30px;
    user-select: none;

    @include mq(pc){
      padding: 0;
    }

  }

}