.l-contact {
  background: url("../images/common/contact-1.jpg") no-repeat center;
  background-size: cover;
  padding: 95px 30px 105px;

  @include mq(tb){
    padding-right: 0;
    padding-left: 0;
  }

  &__container {
    @include mq(tb){
      padding: 0 15px;
    }
  }

  &__menus {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include mq(tb){
      flex-direction: row;
    }
  }

  &__menus {

    @include mq(tb){
      border-left: 1px solid #555555;
      border-right: 1px solid #555555;
      margin-bottom: 62px;
    }
  }


}