
.l-main-visual  {
  position: relative;
  width: 100%;
  background: url("../images/common/bg-shape01.svg") no-repeat right bottom 10%;
  background-size: 80%;

  @include mq(tb){
    background-size: auto;
  }

  @include mq(pc) {
    background-position: right bottom 30%; 
    background-size: auto;
  }


  &__copy {

    @include mq(pc){
      position: absolute;
      top: 0;
      right: 0;
      width: 25%;
      padding-top: 100%;
      display: flex;
      justify-content: center;
    }

    @include mq(xl){
      position:static;
      display: block;
      padding-top: 0;
    }

  }

  &__heading {
    padding: 16px 30px;

    @include mq(pc){
      position: absolute;
      top: 25px;
      left: auto;
      right: 6.5%;
    }

  }



}